body {
  color: #fff;
  background: linear-gradient(135deg, #1a2a33, #3b5360, #4e6b82);
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

h1 {
  text-shadow: 3px 3px 15px #000c;
  font-size: 4rem;
  font-weight: 800;
}

.glass {
  backdrop-filter: blur(12px);
  background: #ffffff1a;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 10px 25px #0009;
}

.input-field {
  color: #fff;
  background: #ffffff0d;
  border: 2px solid #fff3;
  border-radius: 8px;
  outline: none;
  width: 100%;
  padding: 14px;
  font-size: 1rem;
  transition: all .3s;
}

.input-field::placeholder {
  color: #fff9;
}

.input-field:focus {
  border-color: #00b4d8;
  box-shadow: 0 0 15px #00b4d8b3;
}

.btn-primary {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #007bff, #00b4d8);
  border-radius: 8px;
  padding: 12px 20px;
  font-weight: bold;
  transition: all .3s ease-in-out;
  box-shadow: 0 8px 20px #007bff66;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #00b4d8, #007bff);
  transform: scale(1.05);
  box-shadow: 0 12px 25px #007bff99;
}

#resultSection {
  color: #38ef7d;
  font-size: 1.8rem;
  font-weight: bold;
}

@media (width <= 640px) {
  h1 {
    font-size: 2.5rem;
  }

  .glass {
    width: 90%;
  }
}

.slider-container {
  width: 100%;
  padding: 10px;
}

.modern-slider {
  -webkit-appearance: none;
  cursor: pointer;
  background: linear-gradient(to right, #00b4d8 0%, #ccc 0%);
  border-radius: 5px;
  outline: none;
  width: 100%;
  height: 8px;
  transition: background .3s ease-in-out;
}

.modern-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  background: #00b4d8;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: transform .2s ease-in-out;
  box-shadow: 0 4px 8px #00b4d866;
}

.modern-slider::-webkit-slider-thumb:hover {
  transform: scale(1.2);
}

.modern-slider::-moz-range-thumb {
  cursor: pointer;
  background: #00b4d8;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: transform .2s ease-in-out;
}

.modern-slider::-moz-range-thumb:hover {
  transform: scale(1.2);
}

.transaction-table tbody tr {
  transition: background .3s;
}

.transaction-table tbody tr:hover {
  background: #ffffff1a;
  transition: transform .2s ease-in-out, box-shadow .2s ease-in-out;
  transform: scale(1.02);
  box-shadow: 0 6px 20px #0009;
}

#transactionHistory {
  backdrop-filter: blur(8px);
  background: #ffffff1a;
  border-radius: 12px;
  width: 100%;
  max-width: 1440px;
  max-height: 500px;
  margin: 0 auto;
  padding: 20px;
  overflow-y: auto;
  box-shadow: 0 4px 12px #0009;
}

.transaction-table {
  border-collapse: collapse;
  background: #ffffff1a;
  border-radius: 12px;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 4px 12px #0006;
}

.transaction-table thead th {
  text-align: left;
  color: #fff;
  background: #fff3;
  border-bottom: 1px solid #ffffff4d;
  padding: 16px;
  font-size: 1rem;
  font-weight: bold;
}

.transaction-table tbody td {
  color: #ddd;
  border-bottom: 1px solid #ffffff1a;
  padding: 16px;
  font-size: .875rem;
}

.transaction-table tbody tr:last-child td {
  border-bottom: none;
}

@media (width <= 768px) {
  .transaction-table thead {
    display: none;
  }

  .transaction-table tbody td {
    text-align: left;
    border-bottom: 1px solid #fff3;
    width: 100%;
    padding: 8px 12px;
    display: block;
  }

  .transaction-table tbody td:before {
    content: attr(data-label);
    text-transform: uppercase;
    color: #ccc;
    margin-right: 8px;
    font-weight: bold;
  }

  .transaction-table tbody tr:last-child td {
    border-bottom: none;
  }
}

#transactionHistory::-webkit-scrollbar {
  width: 8px;
}

#transactionHistory::-webkit-scrollbar-thumb {
  background: #00b4d8;
  border-radius: 10px;
}

#transactionHistory::-webkit-scrollbar-thumb:hover {
  background: #007bff;
}
